<template>

    <div class="main">
        <h2>Регистрация</h2>
        <form>
            Имя*
            <input name="name" placeholder="Иван"><br>
            Фамилия*
            <input name="surname" placeholder="Иванов"><br>
            E-mail*
            <input name="email" placeholder="example@gmail.com"><br>
            Возраст*
            <input name="age" placeholder="15"><br>
            Пароль*
            <input type="password" name="password" placeholder="1234567"><br>
            Повторите пароль*
            <input type="password" name="password2" placeholder="1234567"><br>
            <button @click="addUser()">Зарегистрироваться</button>
        </form>
    </div>
</template>

<script>
import 'firebase/database'
import { db } from '../main'
export default {
    name: 'Registration',
    methods: {
      addUser(){
        let form = document.forms[0]
        let name = form.elements.name.value
        let surname = form.elements.surname.value
        let email = form.elements.email.value
        let age = form.elements.age.value
        let password = form.elements.password.value
        let password2 = form.elements.password2.value
        //db.collection("users").doc(email).get().then(async function(doc) {
          //if (doc.exists) {
              //await console.log("Document data:", doc.data());
              //alert("2")
          //}
        //})
        if(name.trim() == ''){
          alert("Введите имя")
        }
        else if(surname.trim() == ''){
          alert("Введите фамилию")
        }
        else if(email.trim() == ''){
          alert("Введите email")
        }
        else if(age.trim() == ''){
          alert("введите возраст")
        }
        else if(password.trim() == ''){
          alert("Введите пароль")
        }
        else if(password != password2){
          alert("Пароли не совпадают")
        }
        db.collection("users").doc(email).get().then(async function(doc){
                if (doc.exists) {
                    await alert("Пользователь с таким email уже существует")
                }
                else{
                    db.collection("users").doc(email).set()({
                      name: name,
                      surname: surname,
                      email: email,
                      age: age,
                      password: password,
                    })
                  }
                })
        
      },
      findData(){
        db.collection("users").where("name", "==", "Владимир").get()
        .then(function(querySnapshot) {
            querySnapshot.forEach(function(doc) {
                let docId = doc.id
                //let docData = doc.data()
                return docId
            });
        })
      },
      deliteData(){
        let docId = this.findData()
        alert(docId)
        db.collection("users").doc(docId).delete()
      },
      updateData(){
        db.collection("users").doc("zujwIPpTGKaY3oqyM0V1")
      }
    }
  }


</script>

